import React from 'react'
import styled from 'styled-components'
import SvgTemplate from './SvgTemplate'
import PropTypes from 'prop-types'

const Svg = styled(SvgTemplate)` 
  width: ${props => props.size}px; 
  height: ${props => props.size}px;
`

const ArrowBackIos = ({ 
  className,
  size
}) => ( 
  <Svg viewBox="0 0 24 24" className={className} size={size} fill="currentColor">   
    <path 
      fill="currentColor"
      d="M11.67 3.87L9.9 2.1L0 12L9.9 21.9L11.67 20.13L3.54 12L11.67 3.87Z" 
    />
  </Svg>
)

ArrowBackIos.propTypes = {
  className: PropTypes.string,
  size: PropTypes.number,
}

ArrowBackIos.defaultProps = {
  className: '',
  size: 24
}

export default ArrowBackIos
