import React from 'react'
import classNames from 'classnames'
import styles from '../scss/components/Footer.module.scss'
import Date from './Date'
import Button from './Button'
import PlayBar from './PlayBar'
import { Play as PlayIcon, Pause as PauseIcon } from '../icons'
import { useDispatch, useSelector } from 'react-redux'
import { setTimeDirectionStatus } from '../redux/actions/timeDirectionActions'
import { setPause } from '../redux/actions/pauseActions'
import { openPopup } from '../redux/actions/popupActions'
import ChangeDateTimePopup from '../popups/ChangeDateTimePopup'
import Speed from './Speed'

const Footer = ({
  className=''
}) => {

  const timeDirectionStatus = useSelector(state => state.timeDirectionStatus)
  const pause = useSelector(state => state.pause)
  const time = useSelector(state => state.time)
  const date = useSelector(state => state.date)

  const dispatch = useDispatch()

  return (
    <div className={classNames(className, styles.root)}>
      
      <div className={styles.dateWrap}>
        <Date 
          hh={time.hh} 
          mm={time.mm} 
          ss={time.ss} 
          date={date} 
          onClick={() => dispatch(openPopup(<ChangeDateTimePopup />))} 
          className={styles.date}
        />
      </div>
      
      <PlayBar>
        <Button 
          size='md' 
          uppercase 
          active={timeDirectionStatus === 'backward'}
          onClick={() => dispatch(setTimeDirectionStatus('backward'))}
        >
          Назад
        </Button>
        <Button 
          size='md' 
          uppercase 
          active={pause}
          onClick={() => dispatch(setPause(!pause))}
        >
          {pause ? <PlayIcon /> : <PauseIcon /> }
        </Button>
        <Button 
          size='md' 
          uppercase 
          active={timeDirectionStatus === 'forward'}
          onClick={() => dispatch(setTimeDirectionStatus('forward'))}
        >
          Вперёд
        </Button>
      </PlayBar>

      <div className={styles.speedWrap}>
        <Speed />
      </div>
    </div>
  )
}

export default Footer
