import React, { useState } from 'react'
import classNames from 'classnames'
import styles from '../scss/popups/ChangeDateTimePopup.module.scss'
import Button from '../components/Button'
import { useDispatch, useSelector } from 'react-redux'
import { closePopup } from '../redux/actions/popupActions'

const ChangeDateTimePopup = ({
  className
}) => {

  const time = useSelector(state => state.time)
  const date = useSelector(state => state.date)
  
  const dispatch = useDispatch()

  const [hh, setHH] = useState(time.hh)
  const [mm, setMM] = useState(time.mm)
  const [ss, setSS] = useState(time.ss)
  
  const [_date, set_Date] = useState(date)

  const [validation, setValidation] = useState('')

  const timeDirectionStatus = useSelector(state => state.timeDirectionStatus)

  const checkValidation = () => {
    if (hh === '' || Number(hh) > 24 || Number(hh) < 0) {
      setValidation('Выберите правильный формат времени')
      return false
    }
    if (mm === '' || Number(mm) > 60 || Number(mm) < 0) {
      setValidation('Выберите правильный формат времени')
      return false
    }
    if (ss === '' || Number(ss) > 60 || Number(ss) < 0) {
      setValidation('Выберите правильный формат времени')
      return false
    }
    return true
  }

  const handleSelectDateTime = () => {

    if(!checkValidation()) return

    var seconds = new Date(_date+"T"+hh+":"+mm+":"+ss+".000Z").getTime() / 1000

    fetch('http://192.168.42.165:5000/v1/satelliteio/set?seconds='+seconds+'&status='+timeDirectionStatus, { method: 'POST' })

    dispatch(closePopup())
  }

  return (
    <div className={classNames(className, styles.root)}>

      <input 
        className={styles.input_time}
        type="date" 
        value={_date}
        onChange={e => set_Date(e.target.value)}
      />

      <div className={styles.timePlaceholoders}>
        <span>
          hh 
        </span>  
        <span /> 
        <span>
          mm 
        </span>  
        <span />
        <span>
          ss 
        </span>  
      </div>


      <div className={styles.time}>

        <input 
          type="text"
          value={hh}
          onChange={e => {
            if (!isNaN(e.target.value))
              setHH(e.target.value)
          }}
          className={styles.input}
        />
        <span className={styles.dots}>:</span>
        <input 
          type="text"
          value={mm}
          onChange={e => {
            if (!isNaN(e.target.value))
              setMM(e.target.value)
          }}
          className={styles.input}
        />
        <span className={styles.dots}>:</span>
        <input 
          type="text"
          value={ss}
          onChange={e => {
            if (!isNaN(e.target.value))
              setSS(e.target.value)
          }}
          className={styles.input}
        />

      </div>
      
      {validation !== '' &&
        <div className={styles.validation}>
          {validation}
        </div>
      }
      
      <div className={styles.btn_group}>

        <Button size='lg' className={styles.btn} onClick={() => dispatch(closePopup())}>
          Отмена
        </Button>

        <Button size='lg' className={styles.btn} onClick={() => handleSelectDateTime()}>
          Выбрать дату
        </Button>

      </div>

      
    </div>
  )
}

export default ChangeDateTimePopup
