import classNames from 'classnames'
import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import Earth from './components/Earth'
import Footer from './components/Footer'
import MenuButton from './components/MenuButton'
import Popup from './components/Popup'
import Sidebar from './components/Sidebar'
import styles from './scss/App.module.scss'

const App = () => {

  const [sidebarActive, setSidebarAcitve] = useState(false)

  // const speed = useSelector(state => state.speed)
  const timeDirectionStatus = useSelector(state => state.timeDirectionStatus)

  const time = useSelector(state => state.time)
  const date = useSelector(state => state.date)

  useEffect(() => {

    const handleWindowClick = e => {
      if (!e.target.closest('.'+styles.sidebar) && !e.target.closest('.'+styles.menuBtn))
        return setSidebarAcitve(false)
    }

    window.addEventListener('click', handleWindowClick)
    return () => window.removeEventListener('click', handleWindowClick)
  }, [])

  // useEffect(() => {
  //   fetch('http://192.168.42.165:5000/v1/satelliteio/speed='+speed, {
  //     method: 'POST'
  //   })
  // }, [speed])

  useEffect(() => {

    var seconds = new Date(date+"T"+time.hh+":"+time.mm+":"+time.ss+".000Z").getTime() / 1000

    fetch('http://192.168.42.165:5000/v1/satelliteio/set?seconds='+seconds+'&status='+timeDirectionStatus, { method: 'POST' })
  }, [timeDirectionStatus, date, time])
  
  return (
    <>

      <Earth />

      <Popup />
      
      <div className={styles.root}>

        {!sidebarActive && <MenuButton onClick={() => setSidebarAcitve(true)} className={styles.menuBtn} />}

        <Footer 
          className={styles.footer} 
        />
        
        <Sidebar 
          className={classNames(
            styles.sidebar, 
            sidebarActive && styles['sidebar--open']
          )} 
          onExit={() => setSidebarAcitve(false)}
        />

      </div>
    </>
  )
}

export default App
