import React from 'react'
import classNames from 'classnames'
import styles from '../scss/components/Speed.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import { setSpeed } from '../redux/actions/speedActions'

const Speed = ({
  className
}) => {

  const speed = useSelector(state => state.speed)
  const dispatch = useDispatch()

  return (
    <div className={classNames(className, styles.root)}>
      
      <div className={styles.title}>
        <span>speed</span>  
        <span>
          <input 
            value={speed} 
            type="text" 
            onChange={e => {
              if (isNaN(e.target.value)) return 
              if (Number(e.target.value) > 1000000) return
              if (e.target.value === '')
                dispatch(setSpeed('1'))
              else
                dispatch(setSpeed(e.target.value))
            }} 
          />
        </span>  
      </div>

      <input 
        type="range" 
        className={styles.range}
        value={speed}
        min='-1000000'
        max='1000000'
        onChange={e => dispatch(setSpeed(e.target.value))} 
      />
      
    </div>
  )
}

export default Speed
