import { createStore, combineReducers, applyMiddleware, compose } from 'redux'
import thunk from 'redux-thunk'

import timeDirectionStatusReducer from './timeDirectionStatusReducer'
import popupReducer from './popupReducer'
import pauseReducer from './pauseReducer'
import timeReducer from './timeReducer'
import dateReducer from './dateReducer'
import speedReducer from './speedReducer'

const middleware = [thunk]

const localStorageItemName = 'paymona-bi-state'

// const saveToLocalStorage = state => {
//   try {
//     const serializedState = JSON.stringify(state)
//     localStorage.setItem(localStorageItemName, serializedState)
//   } catch (e) {
//     console.log(e) 
//   }
// }

const loadFromLocalStorage = () => {
  try {
    const serializedState = localStorage.getItem(localStorageItemName)
    if (serializedState === null) return undefined
    return JSON.parse(serializedState)
  } catch (e) {
    console.log(e)
    return undefined
  }
}

const allReducers = combineReducers({
  timeDirectionStatus: timeDirectionStatusReducer,
  popup: popupReducer,
  pause: pauseReducer,
  time: timeReducer,
  date: dateReducer,
  speed: speedReducer
})

const presistedState = loadFromLocalStorage()

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose

const store = createStore(
  allReducers,
  presistedState,
  composeEnhancers(applyMiddleware(...middleware))
)

// store.subscribe(() => saveToLocalStorage({
//   pause: store.getState().pause
// }))

export default store