import { CLOSE_POPUP, OPEN_POPUP } from '../types'

const popupReducer = (state = {active: false, content: ''}, action) => {
  switch(action.type) {
    case OPEN_POPUP: 
      return {active: true, content: action.payload}
    case CLOSE_POPUP: 
      return {active: false, content: ''}
    default: 
      return state
  }
}

export default popupReducer