import React from 'react'
import classNames from 'classnames'
import styles from '../scss/components/Date.module.scss'
import Button from './Button'

const Date = ({
  className='',
  hh='',
  mm='',
  ss='',
  date='',
  onClick=()=>{}
}) => {
  return (
    <div className={styles.wrap}>
      <button className={classNames(className, styles.root)} onClick={onClick}>
        <div className={styles.row}>
          {hh}<span>:</span>{mm}<span>:</span>{ss}Z
        </div>
        <div className={styles.row}>
          {date}
        </div>
      </button>
      <Button 
        className={styles.refresh_btn}
        onClick={() => {
          console.log('hello')
          fetch('http://192.168.42.165:5000/v1/satelliteio/set?seconds=0&status=1', { method: 'POST' })
        }}
      >
        Обновить
      </Button>
    </div>
  )
}

export default Date
